html {
  background-color: #c9c9c9;
}

@font-face {
  src: url('./fonts/TravelingTypewriter.ttf');
  font-family: Traveling Typewriter;
}

.App {
  text-align: center;
  font-family: Traveling Typewriter;
}

.Container {
  margin: auto;
  margin-top: 5%;
  width: 90%;
  font-size: calc(3px + 2vmin);
}

.Container-flex {
  display: flex;
  flex-wrap: wrap;
}

.Container-left {
  text-align: left;
  font-family: Arial;
  padding: 2%;
  width: 45%;
  float: left;
  border: solid black 1px;
}

.Container-right {
  text-align: left;
  font-family: Arial;
  padding: 2%;
  width: 45%;
  float: right;
  border: solid black 1px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Navbar {
  margin-top: 3%;
  margin: auto;
  width: 100%;
  height: 80px;
  position: relative;
  background-color: #181A1B;
}

.Navbar-logo {
  vertical-align: middle;
  height: 70px;
  cursor: pointer;
}

.Navbar-navigation {
  display: flex;
  height: 100%;
  align-items: center;
  padding: 0 1rem;
}

.Navbar-item {
  color: #2196F3;
  text-decoration: none;
  display: inline-block;
  padding: 1rem;
}

.Navbar-item:hover,
.Navbar-item:active {
  color: white;
  cursor: pointer;
}

.Navbar-items ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.Navbar-spacer {
  flex: 1;
}

.Toggle-Button {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 30px;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  box-sizing: border-box;
}

.Toggle-Button:focus {
  outline: none;
}

.Toggle-Button-line {
  width: 40px;
  height: 3px;
  background: gainsboro;
}

.Button-avaa-aanestys {
  margin-top: 5%;
  width: 150px;
  height: 70px;
  border: none;
}

.Button-avaa-aanestys:hover {
  background-color: #2196F3;
}

.Backdrop {
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.3);
  z-index: 100;
}

.Side-Drawer {
  height: 100vh;
  background-color: #181A1B;
  box-shadow: 2px 0px 7px rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  width: 70%;
  max-width: 400px;
  z-index: 200;
  transform: translateX(-100%);
  transition: transform 0.3s ease-out;
}

.Side-Drawer.open {
  transform: translateX(0);
}

.Side-Drawer ul {
  height: 100%;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.Side-Drawer-item {
  text-align: left;
  color: #2196F3;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  font-size: calc(15px + 1vmin);
  padding: 1rem;
}

.Side-Drawer-item:hover, 
.Side-Drawer-item:active {
  color: white;
}

.Side-Drawer li {
  margin: 0.5rem 0;
}

@media (max-width: 900px) {
  .Navbar-items {
    display: none;
  }
}

@media (min-width: 901px) {
  .Toggle-Button {
    display: none;
  }
}

.Line-separator-full {
  height: 2px;
  background-color: black;
  width: 100%;
}

.Loading-animation {
  margin-top: 10%;
  width: 10vw;
}

.Choice-button-left {
  text-align: center;
  font-family: Traveling Typewriter;
  width: 200px;
  max-width: 50%;
  height: 80px;
  border: solid #2196F3 2px;
  margin-top: 4%;
  float: left;
}

.Choice-button-right {
  text-align: center;
  font-family: Traveling Typewriter;
  width: 200px;
  max-width: 50%;
  height: 80px;
  border: solid #2196F3 2px;
  margin-top: 4%;
  float: right;
}

.Choice-button-left:hover, .Choice-button-right:hover {
  background-color: #2196F3;
  border: solid black 2px;
}

.Heading {
  font-size: calc(10px + 2vmin);
  margin-top: 5%;
}

.Topic-item {
  margin-top: 3%;
}

.Chosen-img {
  text-align: center;
  margin-top: 5%;
  max-width: 55px;
}

.Loading-animation {
  max-width: 30%;
}

.Error, .Success {
  margin: 10px 0px;
  padding:12px;
}

.Error {
  background-color: #FFD2D2;
  color: #D8000C;
}

.Success {
  color: #2196F3;
  background-color: #d1ebff;
}

.Aihe-input {
  background-color: #181A1B;
  height: 50px;
  width: 50%;
  font-size: larger;
  color: white;
}

.Add-button {
  font-family: Traveling Typewriter;
  width: 100px;
  height: 60px;
  border: solid #2196F3 2px;
  margin-top: 4%;
}

.Add-button:hover {
  background-color: #2196F3;
  border: solid black 1px;
}
